<!-- FeedbackInput.vue -->
<template>
    <div class="Feedback-input">
        <p>AI</p>
        <!--  -->
        <p v-if="isFeedbackLoading">...</p>
        <!--  -->
        <v-text-field  v-else  v-model="input" @keyup.enter="send" placeholder="" min-width="400"/>
    </div>
</template>

<script>
import { mapState } from 'vuex';

export default {
    data() {
        return { input: '' }
    },
    computed: {
        ...mapState({
            isFeedbackLoading: state => state.chessStore.isFeedbackLoading,
        }),
    },
    methods: {
        send() {
            this.$emit('send', this.input)
            this.input = ''
        },
    },
}
</script>
  